body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px 80px;
  min-width: 1100px;
  text-align: center;
  background-image: url(./nature-2940958_1920.jpg);
}

h1 {
  color: #fff;
  letter-spacing: 10px;
}

ol, ul {
  padding-left: 30px;
}

li {
    list-style: none;
    height: 27px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.under {
    float: left;
}

.board {
    float: left;
}

.number-top {
    float: left;
}

.number-side {
    float: left;
}

.number-side .square {
    background-color: #dacec4ad

}

.number-top .square {
    background-color: #dacec4ad
}

.board-box {
    float: left;
    border: solid;
    border-color: #555;
    margin-top: -1px;
    margin-left: -1px;
}

.side {
    float: none;
}

.square {
  background: #ffffffdb;
  border: 1px solid #999;
  float: left;
  font-size: 19px;
  /* font-weight: bold; */
  line-height: 28px;
  height: 28px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 28px;
  color: #555;
}

.isFilled {
    background: #f97a7ae0;
}

.isCrossed {
    background-image: url(./batu.png);
    background-size: contain;
}

.square-top {
    float: none;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: inline-block;
  flex-direction: row;
  background-color: rgba(255,255,255,.4);
  width: 833px;
  margin: 0 auto;
  padding: 60px;
}

.game-board {
  float: left;
}

.game-info {
  float: left;
  padding-left: 80px;
}

.reset-button {
  background: #ffffffdb;
  border: 1px solid #999;
  color: #555;
  margin: 25px;
  width: 100px;
  height: 40px;
  font-size: 14px;
}

.message {
  margin: 10px;
  width:150px;
  font-size: 18px;
  white-space: pre;
  color: #272626;
}

.ticket {
  width: 170px;
  margin-top: 15px;
}